@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@100;600&display=swap');

.portfolioBlog{
    /* color: red; */
    font-family: 'Poppins', sans-serif;
}

.portfolioBlog__header{
    margin-bottom: 40px;
}

.portfolioBlog__posts{
    /* border: 3px solid purple; */
    margin-bottom: 50px;
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
}

.portfolioBlog__post{
    flex: 1 1 45%;
    /* border: 4px solid red; */
    position: relative;
    text-align: center;
}

.porfolioBlog__text a:hover, a:visited, a:link, a:active
{
    text-decoration: none;
}

.portfolioBlog__text{
    color: bold #D8BFD8;
    text-decoration: none;
}

.portfolioBlog__img > img{
    width: 100%;
    height: 280px;
    border-radius: 20px;
    border: 2px solid #7851A9;
}

.portfolioBlog__img{
    overflow: hidden;
    border-radius: 20px;
}

.portfolioBlog__date{
    margin-top:15px;
    margin-bottom: 5px;
    font-size:11px;
}

.porfolioBlog__text{
    font-weight: 600;
    color: #222222;
}

@media screen and (max-width:768px){
    .portfolioBlog__post{
        flex: 1 1 100%;
    }
}